.btn {
  color: #0E1A35;
  font-size: 14px;
  font-weight: 400;
  text-decoration: none;
  border: 1px solid #ACAAC1;
  padding: 0.5em 1.5em;
  border-radius: 2em; }
  .btn:focus, .btn:hover {
    background: #32B39C;
    border-color: #32B39C; }
  .btn:active {
    background: #ACAAC1;
    border-color: #ACAAC1; }

.video-player-wrap {
  padding: 5em 0 3em 0;
  background: #0e1a35;
  color: #ffffff;
  margin-top: 96px;
  position: relative; }

#video_player {
  width: 650px;
  height: 370px;
  zoom: 0.7; }

.video-details {
  font-weight: 300;
  position: relative; }
  .video-details .video-title {
    margin-top: 0.5em; }
  .video-details .video-duration {
    float: left; }
    .video-details .video-duration::before {
      content: '';
      display: inline-block;
      width: 24px;
      height: 24px;
      vertical-align: middle;
      margin-right: 0.5em;
      background: transparent url(/img/ic_query_builder_black_24dp_1x.png) top left no-repeat; }
      .dark .video-details .video-duration::before {
        background: transparent url(/img/ic_query_builder_white_24dp_1x.png) top left no-repeat; }

.episodes-menu {
  text-align: center;
  display: table;
  margin: 30px auto; }
  .episodes-menu a {
    font-size: 20px;
    text-decoration: none;
    padding: 15px 60px;
    display: table-cell;
    background: #fff;
    color: #000;
    width: 205px; }
    .episodes-menu a:hover {
      color: #32B39C; }
    .episodes-menu a:active {
      color: #D8D8D8; }
    .episodes-menu a.active {
      background: #32B39C;
      color: #fff; }
    .episodes-menu a:first-child {
      border-top-left-radius: 11px;
      border-bottom-left-radius: 11px; }
    .episodes-menu a:last-child {
      border-top-right-radius: 11px;
      border-bottom-right-radius: 11px; }

.card.programme-card.episode-card .card-thumbnail {
  position: relative; }

.card.programme-card.episode-card .duration {
  position: absolute;
  font-size: 13px;
  color: #a6a6a6;
  background: #000;
  opacity: 0.85;
  padding: 2px 0px 2px 13px;
  right: 0;
  bottom: 0; }
  .card.programme-card.episode-card .duration .icon, .card.programme-card.episode-card .duration #back-to-landing .arrow, #back-to-landing .card.programme-card.episode-card .duration .arrow {
    zoom: 0.5;
