  max-width: 25%;
  width: 25%;
  padding: 0 10px 20px 10px;
  display: flex; }
  .card.programme-card .shadow {
    box-shadow: 0 1px 4px 0 #A1AAC1;
    border-radius: 0.5em;
    width: 100%;
